.UserProfile
{
    width: 100%;
}

.UserProfile-flex-container
{
    margin-top: 100px;
    margin-bottom: 100px;
    width: 100%;
    display: flex;
}

.UserProfile-margin-block
{
    width: 2%;
    /*height: 10px;*/
}

.UserProfile-left
{
    width: 72%;
    /*height: 10px;*/
}

.UserProfile-right
{
    width: 24%;
    margin-right: 0%;
    margin-left: auto;
    height: 500px;
}

.inner-container
{
    width: 80%;
    margin: 0 auto;
    display: flex;
}

.follows-container
{
    width: 60%;
    margin-right: 0%;
    margin-left: auto;
    height: 240px;
    
}

.top-block
{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 40px;
    font-family: "Quicksand";
    font-weight: 100;
    overflow: scroll
}

.block
{
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 20px;
    font-family: "Quicksand";
    font-weight: 100;
    width: 50%;
    display: flex
}

.bottom-block
{
    margin-bottom: 20px;
    font-size: 20px;
    margin-top: 40px;
    font-family: "Quicksand";
    font-weight: 100;
}

.public-card-container
{
    height: 100px;
    width: 90%;
    margin: 50px auto;
}