
.LoginRegister
{
    height: 100vh;
    width: 100vw;
    /*background-color: red;*/
    display: flex;
    overflow: hidden;
}

.navbar
{
    height: 6%;
    width: 80%;
    margin: 0 auto;
    /*background-color:yellow;*/
    display: flex;
}

.button
{
    width: 50%;
    height: 100%;
    text-align: center;
}

.block
{
    height: 15%;
}

.form
{
    width: 50%;
    height: 70%;
    /*background-color: green;*/
    margin: 0 auto;
}

.right-container
{
    width: 30%;
    height: 100%;
    position: relative;
}

.left-container
{
    padding-top: 2%;
    width: 70%;
}

img
{
    height: 100%;
    width: 100%;
}
.icon-title-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-content: center;
    align-items: center;
    margin: 1em auto;
}
.icon{
    height: "100%";
}
.title{
    font-family: "Quicksand";
    font-size: 50px;
    color: #4E5167;
}
.subtitle{
    font-family: "Quicksand";
    font-size: 30px;
    color: rgba(78, 81, 103, 0.74);
}

